import styled from 'styled-components';

export default styled.div`
height: 100vh;
overflow-y: auto;
border-left: 1px solid ${props => props.theme.border};
background-color: ${props => props.theme.bgColor_2};
.container-slides{
    height: 100%;
    padding: 40px 20px;
    .wrap-iframe{
        text-align: center;
        height: 100%;
        iframe{
            position: relative;
            z-index: 1;
        }
    }
}

`;